'use client'

import { useState, useEffect } from 'react'
import { PageHeader } from '@/components/ui/PageHeader'
import Link from 'next/link'

// ============================================
// Types
// ============================================

interface ImportSource {
  id: string
  name: string
  logo: string
  description: string
  status: 'available' | 'coming_soon' | 'beta'
  features: string[]
}

interface GHLImportProgress {
  status: 'idle' | 'connecting' | 'fetching' | 'importing' | 'completed' | 'error'
  currentStep: string
  totalSteps: number
  currentStepNumber: number
  itemsProcessed: number
  totalItems: number
  errors: any[]
}

interface ImportPreview {
  contacts: number
  pipelines: number
  opportunities: number
  calendars: number
  tags: number
  customFields: number
  conversations: number
  tasks: number
}

// ============================================
// Mock Data
// ============================================

const importSources: ImportSource[] = [
  {
    id: 'gohighlevel',
    name: 'GoHighLevel',
    logo: 'üöÄ',
    description: 'Import contacts, pipelines, opportunities, calendars, and more from GoHighLevel',
    status: 'available',
    features: [
      'Contacts & Custom Fields',
      'Pipelines & Opportunities',
      'Calendars & Appointments',
      'Tags & Segments',
      'Conversations History',
      'Tasks & Notes',
    ],
  },
  {
    id: 'hubspot',
    name: 'HubSpot',
    logo: 'üü†',
    description: 'Migrate your HubSpot CRM data including contacts, deals, and companies',
    status: 'coming_soon',
    features: [
      'Contacts & Companies',
      'Deals & Pipelines',
      'Email Templates',
      'Forms & Landing Pages',
    ],
  },
  {
    id: 'salesforce',
    name: 'Salesforce',
    logo: '‚òÅÔ∏è',
    description: 'Transfer your Salesforce data with full field mapping support',
    status: 'coming_soon',
    features: [
      'Leads & Contacts',
      'Accounts & Opportunities',
      'Cases & Tasks',
      'Custom Objects',
    ],
  },
  {
    id: 'pipedrive',
    name: 'Pipedrive',
    logo: 'üü¢',
    description: 'Import your Pipedrive deals, contacts, and activities',
    status: 'coming_soon',
    features: [
      'Persons & Organizations',
      'Deals & Pipelines',
      'Activities & Notes',
      'Products',
    ],
  },
  {
    id: 'zoho',
    name: 'Zoho CRM',
    logo: 'üî¥',
    description: 'Migrate from Zoho CRM with comprehensive data mapping',
    status: 'coming_soon',
    features: [
      'Leads & Contacts',
      'Deals & Accounts',
      'Tasks & Events',
      'Custom Modules',
    ],
  },
  {
    id: 'csv',
    name: 'CSV Import',
    logo: 'üìÑ',
    description: 'Import data from any source using CSV files with smart field mapping',
    status: 'available',
    features: [
      'Auto Field Detection',
      'Custom Field Mapping',
      'Duplicate Handling',
      'Bulk Import (50k+ rows)',
    ],
  },
]

type ImportStep = 'select' | 'connect' | 'preview' | 'configure' | 'import' | 'complete'

export default function ImportPage() {
  const [currentStep, setCurrentStep] = useState<ImportStep>('select')
  const [selectedSource, setSelectedSource] = useState<ImportSource | null>(null)
  const [isConnecting, setIsConnecting] = useState(false)
  const [isConnected, setIsConnected] = useState(false)
  const [importPreview, setImportPreview] = useState<ImportPreview | null>(null)
  const [importOptions, setImportOptions] = useState({
    importContacts: true,
    importPipelines: true,
    importOpportunities: true,
    importCalendars: true,
    importConversations: true,
    importTasks: true,
    importTags: true,
    importCustomFields: true,
    mergeExistingContacts: true,
    skipDuplicates: true,
  })
  const [importProgress, setImportProgress] = useState<GHLImportProgress>({
    status: 'idle',
    currentStep: '',
    totalSteps: 0,
    currentStepNumber: 0,
    itemsProcessed: 0,
    totalItems: 0,
    errors: [],
  })
  const [importResult, setImportResult] = useState<any>(null)

  const handleSelectSource = (source: ImportSource) => {
    if (source.status !== 'available') return
    setSelectedSource(source)
    setCurrentStep('connect')
  }

  const handleConnect = async () => {
    setIsConnecting(true)
    
    // Simulate OAuth connection
    await new Promise(resolve => setTimeout(resolve, 2000))
    
    setIsConnecting(false)
    setIsConnected(true)
    
    // Simulate fetching preview data
    setImportPreview({
      contacts: 2847,
      pipelines: 5,
      opportunities: 423,
      calendars: 3,
      tags: 48,
      customFields: 24,
      conversations: 12453,
      tasks: 156,
    })
    
    setCurrentStep('preview')
  }

  const handleStartImport = async () => {
    setCurrentStep('import')
    
    // Simulate import process
    const steps = [
      { name: 'Tags', items: importPreview?.tags || 0 },
      { name: 'Custom Fields', items: importPreview?.customFields || 0 },
      { name: 'Contacts', items: importPreview?.contacts || 0 },
      { name: 'Pipelines', items: importPreview?.pipelines || 0 },
      { name: 'Opportunities', items: importPreview?.opportunities || 0 },
      { name: 'Calendars', items: importPreview?.calendars || 0 },
    ]
    
    setImportProgress({
      status: 'importing',
      currentStep: 'Starting import...',
      totalSteps: steps.length,
      currentStepNumber: 0,
      itemsProcessed: 0,
      totalItems: steps.reduce((sum, s) => sum + s.items, 0),
      errors: [],
    })

    let totalProcessed = 0
    
    for (let i = 0; i < steps.length; i++) {
      const step = steps[i]
      setImportProgress(prev => ({
        ...prev,
        currentStep: `Importing ${step.name}...`,
        currentStepNumber: i + 1,
      }))
      
      // Simulate importing items
      for (let j = 0; j < step.items; j += Math.ceil(step.items / 10)) {
        await new Promise(resolve => setTimeout(resolve, 200))
        totalProcessed += Math.min(Math.ceil(step.items / 10), step.items - j)
        setImportProgress(prev => ({
          ...prev,
          itemsProcessed: totalProcessed,
        }))
      }
    }
    
    // Complete
    setImportProgress(prev => ({
      ...prev,
      status: 'completed',
      currentStep: 'Import completed!',
    }))
    
    setImportResult({
      success: true,
      summary: {
        contacts: { imported: importPreview?.contacts || 0, skipped: 0, errors: 0 },
        pipelines: { imported: importPreview?.pipelines || 0, skipped: 0, errors: 0 },
        opportunities: { imported: importPreview?.opportunities || 0, skipped: 0, errors: 0 },
        calendars: { imported: importPreview?.calendars || 0, skipped: 0, errors: 0 },
        tags: { imported: importPreview?.tags || 0, skipped: 0, errors: 0 },
        customFields: { imported: importPreview?.customFields || 0, skipped: 0, errors: 0 },
      },
      duration: 45000,
    })
    
    setCurrentStep('complete')
  }

  const resetImport = () => {
    setCurrentStep('select')
    setSelectedSource(null)
    setIsConnected(false)
    setImportPreview(null)
    setImportProgress({
      status: 'idle',
      currentStep: '',
      totalSteps: 0,
      currentStepNumber: 0,
      itemsProcessed: 0,
      totalItems: 0,
      errors: [],
    })
    setImportResult(null)
  }

  const steps = [
    { id: 'select', label: 'Select Source', icon: '1' },
    { id: 'connect', label: 'Connect', icon: '2' },
    { id: 'preview', label: 'Preview', icon: '3' },
    { id: 'configure', label: 'Configure', icon: '4' },
    { id: 'import', label: 'Import', icon: '5' },
    { id: 'complete', label: 'Complete', icon: '‚úì' },
  ]

  const currentStepIndex = steps.findIndex(s => s.id === currentStep)

  return (
    <div className="space-y-6">
      <PageHeader
        title="Import Data"
        description="Migrate your data from other CRM platforms to RISIVO"
        icon="üì•"
        actions={
          <Link 
            href="/sub-account/settings"
            className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg font-medium text-sm hover:bg-gray-50 flex items-center gap-2"
          >
            <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
            </svg>
            Back to Settings
          </Link>
        }
      />

      {/* Progress Steps */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
        <div className="flex items-center justify-between">
          {steps.map((step, index) => (
            <div key={step.id} className="flex items-center">
              <div className={`flex items-center justify-center w-10 h-10 rounded-full font-semibold text-sm transition-colors ${
                index < currentStepIndex
                  ? 'bg-green-500 text-white'
                  : index === currentStepIndex
                  ? 'bg-purple-600 text-white'
                  : 'bg-gray-200 text-gray-500'
              }`}>
                {index < currentStepIndex ? '‚úì' : step.icon}
              </div>
              <span className={`ml-2 text-sm font-medium ${
                index <= currentStepIndex ? 'text-gray-900' : 'text-gray-400'
              }`}>
                {step.label}
              </span>
              {index < steps.length - 1 && (
                <div className={`w-12 md:w-24 h-1 mx-4 rounded ${
                  index < currentStepIndex ? 'bg-green-500' : 'bg-gray-200'
                }`} />
              )}
            </div>
          ))}
        </div>
      </div>

      {/* Step Content */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
        {/* Step 1: Select Source */}
        {currentStep === 'select' && (
          <div className="space-y-6">
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Select Import Source</h3>
              <p className="text-gray-600">Choose the platform you want to import data from</p>
            </div>

            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
              {importSources.map((source) => (
                <button
                  key={source.id}
                  onClick={() => handleSelectSource(source)}
                  disabled={source.status !== 'available'}
                  className={`relative p-6 rounded-xl border-2 text-left transition-all ${
                    source.status === 'available'
                      ? 'border-gray-200 hover:border-purple-300 hover:shadow-md cursor-pointer'
                      : 'border-gray-100 bg-gray-50 cursor-not-allowed opacity-60'
                  }`}
                >
                  {source.status === 'coming_soon' && (
                    <span className="absolute top-3 right-3 px-2 py-1 bg-amber-100 text-amber-700 text-xs font-medium rounded-full">
                      Coming Soon
                    </span>
                  )}
                  {source.status === 'beta' && (
                    <span className="absolute top-3 right-3 px-2 py-1 bg-blue-100 text-blue-700 text-xs font-medium rounded-full">
                      Beta
                    </span>
                  )}
                  
                  <div className="text-4xl mb-3">{source.logo}</div>
                  <h4 className="font-semibold text-gray-900 mb-1">{source.name}</h4>
                  <p className="text-sm text-gray-500 mb-4">{source.description}</p>
                  
                  <div className="space-y-1">
                    {source.features.slice(0, 3).map((feature, index) => (
                      <div key={index} className="flex items-center gap-2 text-sm text-gray-600">
                        <span className="text-green-500">‚úì</span>
                        <span>{feature}</span>
                      </div>
                    ))}
                    {source.features.length > 3 && (
                      <p className="text-sm text-purple-600">+{source.features.length - 3} more</p>
                    )}
                  </div>
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Step 2: Connect */}
        {currentStep === 'connect' && selectedSource && (
          <div className="max-w-2xl mx-auto space-y-6">
            <div className="text-center">
              <div className="text-6xl mb-4">{selectedSource.logo}</div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">
                Connect to {selectedSource.name}
              </h3>
              <p className="text-gray-600">
                Authorize RISIVO to access your {selectedSource.name} account to import your data
              </p>
            </div>

            {!isConnected ? (
              <div className="space-y-4">
                <div className="bg-blue-50 border border-blue-200 rounded-xl p-4">
                  <div className="flex items-start gap-3">
                    <span className="text-blue-500 text-xl">‚ÑπÔ∏è</span>
                    <div>
                      <h4 className="font-medium text-blue-900">Secure Connection</h4>
                      <p className="text-sm text-blue-700 mt-1">
                        We use OAuth 2.0 for secure authentication. Your credentials are never stored.
                        You can revoke access at any time from your {selectedSource.name} settings.
                      </p>
                    </div>
                  </div>
                </div>

                <div className="bg-gray-50 rounded-xl p-6">
                  <h4 className="font-semibold text-gray-900 mb-3">What we'll import:</h4>
                  <div className="grid grid-cols-2 gap-2">
                    {selectedSource.features.map((feature, index) => (
                      <div key={index} className="flex items-center gap-2 text-sm text-gray-600">
                        <span className="text-green-500">‚úì</span>
                        <span>{feature}</span>
                      </div>
                    ))}
                  </div>
                </div>

                <div className="flex items-center justify-center gap-4">
                  <button
                    onClick={() => setCurrentStep('select')}
                    className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-medium hover:bg-gray-50"
                  >
                    Back
                  </button>
                  <button
                    onClick={handleConnect}
                    disabled={isConnecting}
                    className="px-6 py-3 bg-gradient-to-r from-purple-600 to-indigo-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-indigo-700 disabled:opacity-50 flex items-center gap-2"
                  >
                    {isConnecting ? (
                      <>
                        <svg className="animate-spin w-5 h-5" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                        </svg>
                        Connecting...
                      </>
                    ) : (
                      <>
                        <span>Connect to {selectedSource.name}</span>
                        <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 5l7 7m0 0l-7 7m7-7H3" />
                        </svg>
                      </>
                    )}
                  </button>
                </div>
              </div>
            ) : (
              <div className="text-center">
                <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <span className="text-green-600 text-3xl">‚úì</span>
                </div>
                <h4 className="font-semibold text-gray-900 mb-2">Connected Successfully!</h4>
                <p className="text-gray-600">Fetching your data preview...</p>
              </div>
            )}
          </div>
        )}

        {/* Step 3: Preview */}
        {currentStep === 'preview' && importPreview && selectedSource && (
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-1">Import Preview</h3>
                <p className="text-gray-600">Review the data that will be imported from {selectedSource.name}</p>
              </div>
              <div className="flex items-center gap-2 px-3 py-1.5 bg-green-100 text-green-700 rounded-full text-sm font-medium">
                <span className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
                Connected
              </div>
            </div>

            <div className="grid md:grid-cols-4 gap-4">
              <div className="bg-gradient-to-br from-blue-50 to-indigo-50 rounded-xl p-5 border border-blue-100">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üë•</span>
                  <span className="text-sm text-gray-600">Contacts</span>
                </div>
                <p className="text-3xl font-bold text-gray-900">{importPreview.contacts.toLocaleString()}</p>
              </div>
              <div className="bg-gradient-to-br from-purple-50 to-pink-50 rounded-xl p-5 border border-purple-100">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üìä</span>
                  <span className="text-sm text-gray-600">Pipelines</span>
                </div>
                <p className="text-3xl font-bold text-gray-900">{importPreview.pipelines}</p>
              </div>
              <div className="bg-gradient-to-br from-green-50 to-emerald-50 rounded-xl p-5 border border-green-100">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üí∞</span>
                  <span className="text-sm text-gray-600">Opportunities</span>
                </div>
                <p className="text-3xl font-bold text-gray-900">{importPreview.opportunities.toLocaleString()}</p>
              </div>
              <div className="bg-gradient-to-br from-amber-50 to-orange-50 rounded-xl p-5 border border-amber-100">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üìÖ</span>
                  <span className="text-sm text-gray-600">Calendars</span>
                </div>
                <p className="text-3xl font-bold text-gray-900">{importPreview.calendars}</p>
              </div>
            </div>

            <div className="grid md:grid-cols-4 gap-4">
              <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600">Tags</span>
                  <span className="font-semibold text-gray-900">{importPreview.tags}</span>
                </div>
              </div>
              <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600">Custom Fields</span>
                  <span className="font-semibold text-gray-900">{importPreview.customFields}</span>
                </div>
              </div>
              <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600">Conversations</span>
                  <span className="font-semibold text-gray-900">{importPreview.conversations.toLocaleString()}</span>
                </div>
              </div>
              <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600">Tasks</span>
                  <span className="font-semibold text-gray-900">{importPreview.tasks}</span>
                </div>
              </div>
            </div>

            {/* Import Options */}
            <div className="bg-gray-50 rounded-xl p-6">
              <h4 className="font-semibold text-gray-900 mb-4">Import Options</h4>
              <div className="grid md:grid-cols-2 gap-4">
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importContacts}
                    onChange={(e) => setImportOptions({ ...importOptions, importContacts: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Contacts ({importPreview.contacts.toLocaleString()})</span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importPipelines}
                    onChange={(e) => setImportOptions({ ...importOptions, importPipelines: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Pipelines ({importPreview.pipelines})</span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importOpportunities}
                    onChange={(e) => setImportOptions({ ...importOptions, importOpportunities: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Opportunities ({importPreview.opportunities})</span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importCalendars}
                    onChange={(e) => setImportOptions({ ...importOptions, importCalendars: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Calendars ({importPreview.calendars})</span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importTags}
                    onChange={(e) => setImportOptions({ ...importOptions, importTags: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Tags ({importPreview.tags})</span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.importCustomFields}
                    onChange={(e) => setImportOptions({ ...importOptions, importCustomFields: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <span className="text-gray-700">Import Custom Fields ({importPreview.customFields})</span>
                </label>
              </div>

              <hr className="my-4 border-gray-200" />

              <div className="space-y-3">
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.mergeExistingContacts}
                    onChange={(e) => setImportOptions({ ...importOptions, mergeExistingContacts: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <div>
                    <span className="text-gray-700">Merge with existing contacts</span>
                    <p className="text-xs text-gray-500">Update existing contacts if email matches</p>
                  </div>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={importOptions.skipDuplicates}
                    onChange={(e) => setImportOptions({ ...importOptions, skipDuplicates: e.target.checked })}
                    className="w-5 h-5 rounded text-purple-600"
                  />
                  <div>
                    <span className="text-gray-700">Skip duplicates</span>
                    <p className="text-xs text-gray-500">Don't import contacts that already exist</p>
                  </div>
                </label>
              </div>
            </div>

            <div className="flex items-center justify-between pt-4">
              <button
                onClick={() => setCurrentStep('connect')}
                className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-medium hover:bg-gray-50"
              >
                Back
              </button>
              <button
                onClick={handleStartImport}
                className="px-6 py-3 bg-gradient-to-r from-purple-600 to-indigo-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-indigo-700 flex items-center gap-2"
              >
                <span>Start Import</span>
                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 5l7 7m0 0l-7 7m7-7H3" />
                </svg>
              </button>
            </div>
          </div>
        )}

        {/* Step 5: Import Progress */}
        {currentStep === 'import' && (
          <div className="max-w-2xl mx-auto space-y-6">
            <div className="text-center">
              <div className="relative w-24 h-24 mx-auto mb-4">
                <svg className="w-24 h-24 transform -rotate-90">
                  <circle
                    className="text-gray-200"
                    strokeWidth="8"
                    stroke="currentColor"
                    fill="transparent"
                    r="40"
                    cx="48"
                    cy="48"
                  />
                  <circle
                    className="text-purple-600 transition-all duration-300"
                    strokeWidth="8"
                    strokeDasharray={251.2}
                    strokeDashoffset={251.2 - (251.2 * (importProgress.itemsProcessed / Math.max(importProgress.totalItems, 1)))}
                    strokeLinecap="round"
                    stroke="currentColor"
                    fill="transparent"
                    r="40"
                    cx="48"
                    cy="48"
                  />
                </svg>
                <div className="absolute inset-0 flex items-center justify-center">
                  <span className="text-2xl font-bold text-gray-900">
                    {Math.round((importProgress.itemsProcessed / Math.max(importProgress.totalItems, 1)) * 100)}%
                  </span>
                </div>
              </div>
              
              <h3 className="text-xl font-semibold text-gray-900 mb-2">
                {importProgress.currentStep}
              </h3>
              <p className="text-gray-600">
                Step {importProgress.currentStepNumber} of {importProgress.totalSteps} ‚Ä¢ 
                {importProgress.itemsProcessed.toLocaleString()} of {importProgress.totalItems.toLocaleString()} items
              </p>
            </div>

            <div className="space-y-2">
              <div className="h-3 bg-gray-200 rounded-full overflow-hidden">
                <div
                  className="h-full bg-gradient-to-r from-purple-500 to-indigo-500 rounded-full transition-all duration-300"
                  style={{ width: `${(importProgress.itemsProcessed / Math.max(importProgress.totalItems, 1)) * 100}%` }}
                />
              </div>
            </div>

            <div className="bg-gray-50 rounded-xl p-4">
              <p className="text-sm text-gray-600 text-center">
                Please don't close this window while the import is in progress.
                This may take a few minutes depending on the amount of data.
              </p>
            </div>
          </div>
        )}

        {/* Step 6: Complete */}
        {currentStep === 'complete' && importResult && (
          <div className="max-w-3xl mx-auto space-y-6">
            <div className="text-center">
              <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-green-600 text-4xl">‚úì</span>
              </div>
              <h3 className="text-2xl font-bold text-gray-900 mb-2">Import Complete!</h3>
              <p className="text-gray-600">
                Your data has been successfully imported from {selectedSource?.name}
              </p>
            </div>

            <div className="grid md:grid-cols-3 gap-4">
              <div className="bg-green-50 rounded-xl p-5 border border-green-200">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üë•</span>
                  <span className="text-sm text-gray-600">Contacts Imported</span>
                </div>
                <p className="text-3xl font-bold text-green-600">
                  {importResult.summary.contacts.imported.toLocaleString()}
                </p>
              </div>
              <div className="bg-green-50 rounded-xl p-5 border border-green-200">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üìä</span>
                  <span className="text-sm text-gray-600">Pipelines Imported</span>
                </div>
                <p className="text-3xl font-bold text-green-600">
                  {importResult.summary.pipelines.imported}
                </p>
              </div>
              <div className="bg-green-50 rounded-xl p-5 border border-green-200">
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">üí∞</span>
                  <span className="text-sm text-gray-600">Opportunities Imported</span>
                </div>
                <p className="text-3xl font-bold text-green-600">
                  {importResult.summary.opportunities.imported.toLocaleString()}
                </p>
              </div>
            </div>

            <div className="bg-gray-50 rounded-xl p-6">
              <h4 className="font-semibold text-gray-900 mb-4">Import Summary</h4>
              <div className="space-y-3">
                {Object.entries(importResult.summary).map(([key, value]: [string, any]) => (
                  <div key={key} className="flex items-center justify-between py-2 border-b border-gray-200 last:border-0">
                    <span className="text-gray-600 capitalize">{key.replace(/([A-Z])/g, ' $1').trim()}</span>
                    <div className="flex items-center gap-4 text-sm">
                      <span className="text-green-600">{value.imported} imported</span>
                      {value.skipped > 0 && <span className="text-amber-600">{value.skipped} skipped</span>}
                      {value.errors > 0 && <span className="text-red-600">{value.errors} errors</span>}
                    </div>
                  </div>
                ))}
              </div>
              <div className="mt-4 pt-4 border-t border-gray-200">
                <p className="text-sm text-gray-500">
                  Total import time: {Math.round(importResult.duration / 1000)} seconds
                </p>
              </div>
            </div>

            <div className="flex items-center justify-center gap-4">
              <button
                onClick={resetImport}
                className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-medium hover:bg-gray-50"
              >
                Import More Data
              </button>
              <Link
                href="/sub-account/contacts"
                className="px-6 py-3 bg-gradient-to-r from-purple-600 to-indigo-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-indigo-700 flex items-center gap-2"
              >
                <span>View Contacts</span>
                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 5l7 7m0 0l-7 7m7-7H3" />
                </svg>
              </Link>
            </div>
          </div>
        )}
      </div>

      {/* AI Assistant Tip */}
      <div className="bg-gradient-to-r from-purple-600 to-indigo-600 rounded-xl p-6 text-white">
        <div className="flex items-start gap-4">
          <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center flex-shrink-0">
            <span className="text-2xl">ü§ñ</span>
          </div>
          <div>
            <h4 className="font-semibold text-lg mb-1">AI Virtual Assistant Can Help!</h4>
            <p className="text-purple-100 mb-3">
              You can also use voice or chat commands to import data. Just say:
            </p>
            <div className="flex flex-wrap gap-2">
              <span className="px-3 py-1 bg-white/20 rounded-full text-sm">"Import my GoHighLevel contacts"</span>
              <span className="px-3 py-1 bg-white/20 rounded-full text-sm">"Migrate from GHL to RISIVO"</span>
              <span className="px-3 py-1 bg-white/20 rounded-full text-sm">"Connect my GHL account"</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
